.navbar {
  background-image: linear-gradient(to right, #1c2237, #485eae, #3f1f1e );
  /* background: #1c2237; */
  min-height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  min-height: 60px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-menu {
  display: flex;
  align-items: center;
  /*justify-content: center;*/
  list-style: none;
  text-align: center;
  justify-content: space-evenly;
}

.container {
  z-index: 1;
  width: 100%;
  max-width: 1300px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 5px;
  padding-left: 5px;
}


.navbar-logo {
  color: #fff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.navbar-icon {
  margin-right: 0.5rem;
}

.nav-item {
  display: flex;
  min-height: 40px;
  border-bottom: 2px solid transparent;
  align-items: center;
}

.nav-item:hover {
  border-bottom: 2px solid #f00946;
}

.fa-bars {
  color: #fff;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 900px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(100vh - 80px);
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #1c2237;
    left: 0;
    opacity: 1;
    transition: all 0.6s ease;
    z-index: 1;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 40%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    color: #f00946;
    transform: scale(1.2);
    transition: all 0.3s ease;
  }
  
  .nav-item:hover {
    border: none;
  }
 
  /* .nav-item {
    width: 100%;
  } */
  
  .navbar-logo {
    position: absolute;
    top: 12px;
    left: 5px;
    /* transform: translate(25%, 50%); */
  }

  .fa-times , .fa-bars {
    color: #fff;
    font-size: 2rem;
  }
  
  .nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* height: 120px; */
  } 
}

