:root{
  --primary: #fff;
}

/* BASE STYLE */
/********************************************************************/
.btn {
  padding: 8px 20px;
  border-radius: 4px;
  outline: none;
  border: none;
  cursor: pointer;
  white-space: nowrap;
}

.btn--primary{
  background-color: var(--primary);
  color: #242424;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  border: 3px solid var(--primary) ;
  transition: all 0.5s ease-out;
}

/* SIZE */
/********************************************************************/
.btn--medium{
  padding: 8px 20px;
  font-size: 18px;
}

.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn--mobile {
  text-align: center;
  border-radius: 4px;
  width: 80%;
  text-decoration: none;
  padding: 14px 20px;
  font-size: 24px;
  background-color: transparent;
  border: 1px solid var(--primary);
  transition: all 0.5s ease-out;
}

.btn--wide{
  padding: 12px 64px;
  font-size: 20px;
}

.btn--large:hover, .btn--medium:hover, .btn--mobile:hover{
  transition: all 0.3s ease-out;
  background: var(--primary);
  color: #242424;
}

.btn--wide:hover {
  color: var(--primary);
  background-color: #f00946;
  transition: all 0.2s ease-out;
}

/* btn-link ()*/
/********************************************************************/
.btn-link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 8px 5px 8px 16px;
  height: 100%;
  width: 100%;
}

/* COLOUR */
/********************************************************************/
.blue {
  background-color: #276afb;
  color: #fff;
  border: none
}

.red {
  background-color: #f00946;
  color: #fff;
  border: none
}

.primary {
  background-color: #242424;
  color: #fff;
  border: none
}

.primary:hover {
  background-color: #fff;
  color: #242424;
  border: none
}

.green {
  background-color: #25ce4a;
  color: #fff;
  border: none
}

.green:hover {
  background-color: #242424;
}
